@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    border: 0;
    outline: 0;
    text-decoration: none;
    font-size: 100%;
    vertical-align: baseline;
    font-family: 'Poppins', sans-serif;
}

html {
    scroll-behavior: smooth;
}

section {
    min-height: 100vh;
    min-width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 0;
}


.base {
    position: fixed;
    top: 0;
    left: 0;
    min-height: 100vh;
    min-width: 100vw;
    overflow: hidden;
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
}

header {
    position: absolute;
    width: 85vw;
    top: 12.5vh;
    z-index: 3;
    padding: 20px 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: rgba(60, 60, 60, 0.1);
    box-shadow: 0 15px 35px rgba(0, 0, 0, 0.05);
    border-radius: 20px;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.25);
    border-top: 1px solid rgba(255, 255, 255, 0.5);
    border-left: 1px solid rgba(255, 255, 255, 0.5);
}

header ul {
    display: flex;
}

header ul li {
    list-style: none;
    margin: 0 1em;
}

header ul li a {
    text-decoration: none;
    font-weight: 500;
    padding: 8px 15px;
    border-radius: 15px;
    color: white;
    font-size: 20px;
}

header ul li a:hover {
    color: black;
    background: #fff;
}

.container {
    position: absolute;
    top: 16vh;
    min-width: 90vw;
    min-height: 75vh;
    max-height: 75vh;
    z-index: -1;
    background: rgba(100, 100, 100, 0.2);
    box-shadow: 0 15px 35px rgba(255, 255, 255, 0.05);
    border-radius: 20px;
}
.projects_container {
    display: flex;
    justify-content: center;
    padding: 6vh;
    overflow: scroll;
}

.projects_container ul {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    grid-gap: 2rem;
}

.project_card {
    padding: 20px;
    min-height: 200px;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    background: rgba(0, 0, 0, 0.1);
    box-shadow: 5px 15px 35px rgba(0, 0, 0, 1);
    border-radius: 15px;
}

.project_card:hover {
    transition: 0.5s;
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
}

.project_card h1 {
    font-size: 1.2em;
    color: white;
}

.project_card p {
    font-size: 1em;
    color: white;
}


.skills_container {
    display: flex;
    justify-content: center;
    padding: 6vh;
    overflow: scroll;
}

.skills_container ul {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 2rem;
}

.skill_card {
    padding: 20px;
    min-height: 100px;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    box-shadow: 5px 15px 35px rgba(0, 0, 0, 1);
    border-radius: 15px;
}

.skill_card span {
    display: flex;
    align-items: center;
}

.skill_card span h1 {
    display: inline;
    color: white;
    font-size: 2em;
    padding: 10px;
    font-weight: normal;
}

.skill_card p {
    color: white;
    font-size: 1em;

}

.skill_card span svg {
    color: #fff;
}

.skill_card span img {
    max-height: 2em;
    max-width: 2em;
    -webkit-filter: brightness(0) invert(1);
            filter: brightness(0) invert(1);
}


.resume_container {
    display: flex;
    justify-content: center;
}

.resume_cards {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin-right: -3em;
}

.resume_card {
    min-width: 300px;
    max-width: 1000px;
    min-height: 200px;
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    -webkit-backdrop-filter: blur(20px);
            backdrop-filter: blur(20px);
    border: 1px solid rgba(255, 255, 255, 0.25);
    border-top: 1px solid rgba(255, 255, 255, 0.5);
    border-left: 1px solid rgba(255, 255, 255, 0.5);
    padding-left: 20px;
    padding-top: 30px;
    flex: 1 1;
    margin: 2rem;
    /*margin-left: rem;*/
    transition: 0.5s;
    display: flex;
    flex-direction: column;
}

.resume_card h2 {
    color: #ededed;
    font-size: 17px;
}

.resume_card p {
    color: #b0b0b0;
}

.resume_card img {
    height: 50px;
    width: 50px;
}

.resume_card svg {
    height: 50px;
    width: 50px;
}

.resume_card:hover {
    transition: 0.5s;
    -webkit-transform: translateY(-2rem);
            transform: translateY(-2rem);
    z-index: 5 !important;
}
